html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
  min-height: 150vh;
}

a,
a:active,
a:visited {
  color: olivedrab;
}

a:hover {
  color: red;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:active,
a:visited {
  text-decoration: none;
}

:root {
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram-start: hsl(37, 97%, 70%);
  --Instagram-end: hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggleBG-light: hsl(230, 22%, 74%);
  --darkBG: hsl(230, 17%, 14%);
  --darkBG-TOP: hsl(232, 19%, 15%);
  --darkBG-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --darkBG-hover: hsl(228, 25%, 27%);
  --lightBG: hsl(0, 0%, 100%);
  --lightBG-hover: hsl(228, 38%, 91%);
  --lightBG-TOP: hsl(225, 100%, 98%);
  --lightBG-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--lightBG);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text2);
  --card-bg: var(--lightBG-card);
  --card-hover: var(--lightBG-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggleBG-light);
  --toggle-button: var(--lightBG);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--darkBG);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--darkBG-card);
    --card-hover: var(--darkBG-hover);
    --toggle: var(--lightBG);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
    --toggle-button: var(--darkBG);
  }
}
body.dark {
  --background: var(--darkBG);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--darkBG-card);
  --card-hover: var(--darkBG-hover);
  --toggle: var(--lightBG);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--darkBG);
}

body.light {
  --background: var(--lightBG);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text2);
  --card-bg: var(--lightBG-card);
  --card-hover: var(--lightBG-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggleBG-light);
  --toggle-button: var(--lightBG);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  margin: 0 2.875rem;
}
@media (max-width: 87.4375em) {
  .container {
    padding: 0;
  }
}

footer {
  margin: 1.5rem;
  text-align: center;
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(----text-color2);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 0.3rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  align-self: center;
  color: var(--toggle);
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
}
.toggle label[for=light] {
  grid-column: 3/4;
  justify-self: start;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
  /* justify-self: center; */
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5625rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  border-radius: 0.75rem;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  background: var(--toggle-bg);
  pointer-events: none;
  z-index: -1;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  pointer-events: none;
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 3px;
  left: calc(100% - 20px);
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  flex: 1;
  background-color: var(--card-bg);
  color: var(--text-color2);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background-color: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--Facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--Twitter);
}
.card--instagram {
  border-image: linear-gradient(to right, var(--Instagram-start), var(--Instagram-end)) 45;
  border-width: 0.3125rem 0 0 0;
  border-style: solid;
}
.card--youtube {
  border-top: 0.3125rem solid var(--YouTube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color2);
  font-weight: bold;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  color: var(--dark-text1);
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: 400;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--LimeGreen);
}
.card__change--down {
  color: var(--BrightRed);
}
.card img {
  margin-right: 0.25rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: end;
  gap: 1.1875rem;
}
.card-grid .card__subtitle {
  justify-self: start;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid .card__count {
  justify-self: start;
  margin-bottom: 0;
}