html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
  min-height: 150vh;
}

a,
a:active,
a:visited {
  color: olivedrab;
}
a:hover {
  color: red;
}
